<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、栈方法</p>
            <p>
                使用栈方法可以将数组模拟成栈（<span class="tip">Stack</span>）的方法。出栈使用 <span class="code">pop()</span>（删除数组最后面的元素）；入栈使用 <span class="code">push()</span>（在数组最后面加入一个元素，需要传参）。
            </p>
            <p class="minititle">二、队列方法</p>
            <p>
                使用队列方法可以将数组模拟成队列（<span class="tip">Queue</span>）的方法。出队使用 <span class="code">shift()</span>（删除在数组最前面的元素；与其对应有一个 <span class="code">unshifit()</span>，效果为在数组最前面加入元素，需要传参）；入队也是使用 <span class="code">push()</span>。
            </p>
            <p class="minititle">三、排序方法</p>
            <p>
                <span class="code">sort()</span> 方法默认按字符串（<span class="tip">String</span>）排序，例如：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr1</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,3,6,9,10</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr1</span><span class="word">.</span><span class="method">sort</span><span class="word">();</span><span class="com"> // arr1 = [1,10,3,6,9] </span></pre>
            <p>
                如果想要按照数字的大小去排序需要改变排序的规则，见下例：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr2</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,3,6,9,10</span><span class="rect">]</span><span class="word">;</span><br><span class="com">// 升序排序函数</span><br><span class="key">function </span><span class="fun">asc</span><span class="word">(value1,value2)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( value1 </span><span class="key">&lt;</span><span class="word"> value2 )</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">-1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">} </span><span class="key">else if</span><span class="word"> ( value1 </span><span class="key">&gt;</span><span class="word"> value2 )</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">} </span><span class="key">else </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">0;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><span class="com">// 降序排序函数</span><br><span class="key">function </span><span class="fun">desc</span><span class="word">(value1,value2)</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( value1 </span><span class="key">&lt;</span><span class="word"> value2 )</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">} </span><span class="key">else if</span><span class="word"> ( value1 </span><span class="key">&gt;</span><span class="word"> value2 )</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">-1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">} </span><span class="key">else </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">0;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><span class="com">// 调用排序方法</span><br><span class="param">arr</span><span class="word">.</span><span class="method">sort</span><span class="word">(</span><span class="fun">asc</span><span class="word">);</span><span class="com">&nbsp;&nbsp;// 升序排法；arr = [1,3,6,9,10]</span><br><span class="param">arr</span><span class="word">.</span><span class="method">sort</span><span class="word">(</span><span class="fun">desc</span><span class="word">);</span><span class="com">&nbsp;// 降序排法；arr = [10,9,6,3,1]</span></pre>
            <p>
                如果数组原来是正序排列，希望变成反序排列的话，直接使用 <span class="code">.reverse()</span> 就可以了。
            </p>
            <p class="minititle">四、操作方法</p>
            <p>
                <span class="code">concat()</span> 方法可以基于当前数组中的所有项创建一个新数组。如果没有传参，则会给原数组创建一个副本并返回。看下面一个例子：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr1</span><span class="key"> = </span><span class="rect">[</span><span class="str">'a'</span><span class="word">,</span><span class="str">'b'</span><span class="word">,</span><span class="str">'c'</span><span class="rect">]</span><span class="word">;</span><br><span class="key">var </span><span class="param">arr2</span><span class="key"> = </span><span class="param">arr1</span><span class="word">.</span><span class="method">concat</span><span class="word">(</span><span class="str">'z'</span><span class="word">,</span><span class="rect">[</span><span class="str">'s'</span><span class="word">,</span><span class="str">'t'</span><span class="word">,</span><span class="str">'r'</span><span class="rect">]</span><span class="word">);</span><br><span class="com">// arr1 = ['a','b','c']；arr2 = ['a','b','c','z','s','t','r']</span></pre>
            <p class="minititle"></p>
            <p>
                <span class="code">slice()</span> 方法基于当前数组中的一个或多个项创建一个新数组。可以接收 1 个或 2 个参数，即要返回项的起始位置和结束位置（一个参数时，表示起始位置到数组末尾）。见下例：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr1</span><span class="key"> = </span><span class="rect">[</span><span class="str">'a'</span><span class="word">,</span><span class="str">'b'</span><span class="word">,</span><span class="str">'c'</span><span class="word">,</span><span class="str">'d'</span><span class="rect">]</span><span class="word">;</span><br><span class="key">var </span><span class="param">arr2</span><span class="key"> = </span><span class="param">arr1</span><span class="word">.</span><span class="method">slice</span><span class="word">(1);</span><span class="com"> // 截取从 1 到 末尾；arr2 = [b','c','d']</span><br><span class="key">var </span><span class="param">arr3</span><span class="key"> = </span><span class="param">arr1</span><span class="word">.</span><span class="method">slice</span><span class="word">(1,3);</span><span class="com"> // 截取从 1 到 2；arr3 = ['b','c']</span><br></pre>
            <p>
                接下来介绍一下 <span class="code">splice()</span> 方法，这个方法可以做到增删改。
            </p>
           <p>
                增：传入 <span class="tip">3</span> 个参数：起始位置，要删除的项数，插入的内容。如 <span class="code">splice(1,0,'aa','bb')</span>（从 <span class="tip">1</span> 位置加入，不删除项）。
            </p>
            <p>
                删：传入 <span class="tip">2</span> 个参数：起始位置，要删除的项数。如 <span class="code">splice(1,2)</span>（从 <span class="tip">1</span> 位置开始删除 <span class="tip">2</span> 项，不插入内容）。
            </p>
            <p>
                改：传入 <span class="tip">3</span> 个参数：起始位置，要删除的项数，插入的内容。如 <span class="code">splice(1,1,'aa')</span>（先删除位置 <span class="tip">1</span> 的值，再往 <span class="tip">1</span> 位置插入）。
            </p>
            <p class="minititle">五、位置方法</p>
            <p>
                <span class="code">indexOf()</span> 和 <span class="code">lastIndexOf()</span>。这个方法对于字符串（<span class="tip">String</span>）也有效。接受两个参数，第一个参数是值，第二个参数是位置（第二个参数不传默认使用 <span class="tip">0</span>）。下面是一个例子：
            </p>
            <pre class="code"><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="str">'a'</span><span class="word">,</span><span class="str">'b'</span><span class="word">,</span><span class="str">'c'</span><span class="word">,</span><span class="str">'d'</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">indexOf</span><span class="word">(</span><span class="str">'b'</span><span class="word">);</span><span class="com"> // 1</span><br><span class="param">arr</span><span class="word">.</span><span class="method">indexOf</span><span class="word">(</span><span class="str">'f'</span><span class="word">);</span><span class="com"> // -1，未查到</span><br><span class="param">arr</span><span class="word">.</span><span class="method">indexOf</span><span class="word">(</span><span class="str">'b'</span><span class="word">,1);</span><span class="com"> // 1</span><br><span class="param">arr</span><span class="word">.</span><span class="method">indexOf</span><span class="word">(</span><span class="str">'b'</span><span class="word">,2);</span><span class="com"> // -1，从位置 2 开查没查到</span></pre>
            <p class="minititle">六、迭代方法</p>
            <p>
                迭代方法的参数都有三个 <span class="code">(item,index,array)</span>（当前值，当前索引，原数组）。
            </p>
            <p>
                <span class="code">every()</span>：对数组每一项运行给定函数，如果每一项对返回 <span class="code">true</span>，则返回 <span class="code">true</span>。可以用于判断数组中的值是否符合规则。例如：
            </p>
            <pre class="code"><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2,</span><span class="str">'a'</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">every</span><span class="word">( e </span><span class="key">=&gt; </span><span class="spe">typeof</span><span class="word"> e</span><span class="key"> ===</span> <span class="str">'string'</span><span class="word"> );</span><span class="com"> // 此例不全是 String 类型，将返回 false</span></pre>
            <p>
                <span class="code">filter()</span>：对数组每一项运行给定函数，该函数会返回 <span class="code">true</span> 的项组成的数组。可以用于判断数组中的值是否符合规则。例如：
            </p>
            <pre class="code"><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">58,70,97,89,43,18,0</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">filter</span><span class="word">( e</span><span class="key"> =&gt; </span><span class="word">e</span><span class="key"> &lt;= </span><span class="word">59 );</span><span class="com"> // 返回60以下的数据 [58,43,18,0]</span></pre>
            <p>
                <span class="code">forEach()</span>：对数组每一项运行给定函数，只是遍历数组，没有返回值。这个方法比较灵活，可以封装成其它几个方法，就不举例了。
            </p>
            <p>
                <span class="code">map()</span>：对数组每一项运行给定函数，返回每次函数调用结果组成的数组。例如：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">58,70,97,89,43,18,0</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">map</span><span class="word">( e</span><span class="key">=> </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( e</span><span class="key"> &lt;= </span><span class="word">60 )</span><span class="key"> return </span><span class="str">'F'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( e</span><span class="key"> &lt;= </span><span class="word">70 )</span><span class="key"> return </span><span class="str">'D'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( e</span><span class="key"> &lt;= </span><span class="word">80 )</span><span class="key"> return </span><span class="str">'C'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( e</span><span class="key"> &lt;= </span><span class="word">90 )</span><span class="key"> return </span><span class="str">'B'</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( e</span><span class="key"> &lt;= </span><span class="word">100 )</span><span class="key"> return </span><span class="str">'A'</span><span class="word">;</span><br><span class="flower">}</span><span class="word">);</span><span class="com"> // 按分数评级 ["F","D","A","B","F","F","F"]</span></pre>
            <p>
                <span class="code">some()</span>：对数组每一项运行给定函数，如果函数任一项返回 <span class="code">true</span>，则返回 <span class="code">true</span>，与 <span class="code">every()</span> 非常相似。例如：
            </p>
            <pre class="code"><span class="key">var</span><span class="param"> arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2,</span><span class="str">'a'</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">some</span><span class="word">( e </span><span class="key">=&gt; </span><span class="spe">typeof</span><span class="word"> e</span><span class="key"> ===</span> <span class="str">'string'</span><span class="word"> );</span><span class="com"> // 此例含有一个 String 类型，将返回 true</span></pre>
            <p class="minititle">七、缩小方法</p>
            <p>
                缩小数组有两个方法：<span class="code">reduce()</span> 和 <span class="code">reduce()</span>。它们都会迭代数组的所有项，构建一个最终的返回值，区别在于 <span class="code">reduce()</span> 从数组第一项开始遍历到最后，而 <span class="code">reduce()</span> 从数组最后一项开始遍历。
            </p>
            <p>
                <span class="code">reduce()</span> 和 <span class="code">reduce()</span> 都接收 <span class="tip">4</span> 个参数：<span class="code">(prev,cur,index,array)</span>（前一个值，当前值，当前索引，原数组）。函数运行的时候 <span class="code">prev</span> 一开始是数组的第一个值，后面使用的是上一个调用函数的引用值（<span class="code">return</span> 出来的值）。
            </p>
            <pre class="code"><span class="key">var </span><span class="param">arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2,3,4,5</span><span class="rect">]</span><span class="word">;</span><br><span class="param">arr</span><span class="word">.</span><span class="method">reduce</span><span class="word">( (prev,cur,index)</span><span class="key"> =&gt; </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">prev</span><span class="key"> * </span><span class="word">cur ;</span><br><span class="flower">}</span><span class="word">);</span><span class="com"> // 叠乘，120</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


