<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="code">onerror</span> 属性常规使用只需要将图片路径放进去就可以了。如果是 <span class="tip">Vue</span> 的 <span class="tip">SPA</span> 的话，直接使用项目中的图片路径是不行的。使用下面的方式即可：
            </p>
            <pre class="code"><span class="arrow">&lt;</span><span class="tag">template</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">div</span><span class="arrow">&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;</span><span class="tag">img&nbsp;</span><span class="attr">:src=</span><span class="word">"imgSrc"&nbsp;</span><span class="attr">:onerror=</span><span class="word">"defaultImgSrc"</span><span class="arrow">/&gt;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="arrow">&lt;/</span><span class="tag">div</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;/</span><span class="tag">template</span><span class="arrow">&gt;</span><br><span class="arrow">&lt;</span><span class="tag">script</span><span class="arrow">&gt;</span><br><span class="key">export default</span><span class="flower"> {</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">data</span><span class="word">()</span>&nbsp;<span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">defaultImgSrc</span><span class="word">&nbsp;:&nbsp;</span><span class="str">'this.src="'</span><span class="key">&nbsp;+&nbsp;</span><span class="key">require</span><span class="word">(</span><span class="str">'./../assets/img/img_default.png'</span><span class="word">)</span><span class="key">&nbsp;+&nbsp;</span><span class="str">'"'</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="arrow">&lt;/</span><span class="tag">script</span><span class="arrow">&gt;</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


