<template>
    <main>
        <section class="main-left">
            <!-- 轮播 -->
            <el-carousel
                    class="_roundImg"
                    :interval="5000"
                    :autoplay="true"
                    @change="swipeChange($event)"
                    arrow="always"
                    :height="carouselHei + 'px'">
                    <el-carousel-item
                            v-for="item in bgImg"
                            :key="item"
                            :name="item.url">
                        <img class="round-img" :src="item.src" alt="item.url" @click="toBlogDetail(item.url+'')">
                        <h3 class="round-title" @click="toBlogDetail(item.url+'')">{{$store.state.initialBlogData[$store.state.initialBlogData.length - item.url].title}}</h3>
                    </el-carousel-item>
            </el-carousel>

            <!-- 最新内容 -->
            <p class="lastest-text">Latest 10</p>
            <article v-for="item in recentTen"
                     @click="$parent.toDetails(item.type,item.url)">
                <h4 class="title">
                    {{item.title}}
                </h4>
                <p class="time">
                    {{item.time}}
                    <el-tag v-for="tag in item.tags"
                            :type="tag.type"
                            :key="tag.type">{{tag.name}}</el-tag>
                    {{distance(item.time)}}天前
                </p>
                <p v-html="returnBrief(item.brief)" class="brief">
                    {{item.brief}}
                </p>
            </article>
        </section>
        <!-- 个人信息 -->
        <section class="main-right">
            <header class="info">
                <div class="head-bg">
                    <img src="./../../assets/img/headBg.png" alt="head-bg"/>
                </div>
                <div class="motto">{{$store.state.motto}}</div>
                <div class="head-img"></div>
                <div class="head-name">
                    <span>{{$store.state.owner}}</span>
                    <el-tooltip class="item" effect="dark" content="https://github.com/xll032" placement="bottom-start">
                        <a href="https://github.com/xll032" target="_blank">
                            <img src="./../../assets/img/git_icon.png" alt="git">
                        </a>
                    </el-tooltip>
                </div>
            </header>
            <div class="type">
                <header>
                    <img src="./../../assets/img/tags_icon.png" alt="tags">
                    <span>技术分类</span>
                </header>
                <el-tag v-for="tag in $store.state.tags" :type="tag.type" :key="tag.name" @click.native="searchForType(tag.name)">{{tag.name + '(' + tagSum(tag.name) + ')'}}</el-tag>
            </div>
        </section>
    </main>
</template>
<script>
    //import img1 from '../../assets/img/1.png';// 不好
    import img2_default from '../../assets/img/2_default.png';
    import img3_default from '../../assets/img/3_default.png';
    import img4_default from '../../assets/img/4_default.png';
    import img5_default from '../../assets/img/5_default.png';

    import img2 from '../../assets/img/2.png';
    import img3 from '../../assets/img/3.png';
    import img4 from '../../assets/img/4.png';
    import img5 from '../../assets/img/5.png';
    //import img6 from '../../assets/img/6.png';
    //import img7 from '../../assets/img/7.png';
    //import img8 from '../../assets/img/8.png';

    const imgArr = [img2,img3,img4,img5];
    export default {
        name: 'home',
        data(){
            const that = this;
            return {
                carouselHei:420,
                bgImg:[
                    {url:'82',src:img2_default},
                    {url:'48',src:img3_default},
                    {url:'33',src:img4_default},
                    {url:'22',src:img5_default},
                ],
                recentTen:that.getRecentBlog()
            }
        },
        methods: {
            // 轮播图切换懒加载图片
            swipeChange(curIndex){
                new Promise( (resolve,reject) => {
                    this.bgImg[curIndex].src = imgArr[curIndex];
                });
            },
            // 推荐最新 10篇
            getRecentBlog(){
                const that = this;
                let _recent = [];
                that.$store.state.initialBlogData.forEach( (e,i) => {
                    if( i <= 10 ) _recent.push(e);
                    else return false;
                });
                return _recent;
            },
            // 跳转至详情页
            toDetails(type,path){
                if( type === 0 ) this.$router.push( '/blog/blog_' + path );
                else if( type === 1 ) this.$router.push( '/demo/demo_' + path );
            },
            // 计算距今的时间（天数）
            distance(time){
                let now = new Date();
                let [ year, month, date ] = [ time.substring(0,4), time.substring(5,7), time.substring(8,10) ];
                let before = new Date( year, month - 1, date );
                return ( ( now - before ) / 1000 / 60 / 60 / 24 ) >> 0;
            },
            // 过滤标签后，固定显示不超过 75 字简介
            returnBrief(val){
            	if( typeof val !== 'string') return '暂无';
            	const maxLength = 75;
                const _val = val.replace(/<[^>]+>/g,"");
                return _val.substring( 0,maxLength ) + ( _val.length < maxLength ? '' : '...' );
            },
            // 按类型查找 blog
            searchForType(name){
                const that = this;
                that.$store.commit('searchForType',name);
                that.$router.push('/blog');
            },
            // 转到博客详情
            toBlogDetail(url){
                this.$router.push('/blog/blog_' + url);
            },
            // 根据屏幕大小 设置轮播图高度
            setCarouselHei(){
                const round = document.getElementsByClassName('_roundImg')[0],
                      minHei = 130;
                let calcHei = '';
                if(round) calcHei = ( parseInt(window.getComputedStyle(round).width) / 2.5 ) >> 0;
                else return false;
                this.carouselHei = calcHei < minHei ? minHei : calcHei;
            },
            // 统计 blog 类型总量
            tagSum(name){
                const that = this;
                let count = 0;
                that.$store.state.initialBlogData.forEach( (e,i) => {
                    e.tags.forEach( (element,index) => {
                       if(element.name === name){
                           count++;
                       }
                    });
                });
                return count;
            },
        },
        mounted(){
            if(location.hash ==='#/home') document.title = '个人博客 by 龙';

            this.setCarouselHei();

            window.onresize = () => this.setCarouselHei();
        }
    }
</script>


