<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、Set</p>
            <p>
                <span class="tip">Set 结构</span> 有点类似于数组，但它没有重复的数据，所有成员的值都是唯一的。可以先创建一个空的 <span class="tip">Set</span>，再用 <span class="code">add()</span> 添加成员，也可在创建一个新的 <span class="tip">Set</span> 的时候传入参数（数组格式）。见下例：
            </p>
            <pre class="code"><span class="com">// 先创建空 Set，再添加成员</span><br><span class="key">const </span><span class="param">s1</span><span class="key"> = </span><span class="key">new </span><span class="method">Set</span><span class="word">();</span><br><span class="rect">[</span><span class="word">1,1,2,2,2,3,4,5,6</span><span class="rect">]</span><span class="word">.</span><span class="method">forEach</span><span class="word">( x</span><span class="key"> =&gt; </span><span class="param">s1</span><span class="word">.</span><span class="method">add</span><span class="word">(x) );</span><span class="com"> // [1,2,3,4,5,6]</span><br><br><span class="com">// 创建 Set 的时候，传参添加成员</span><br><span class="key">const </span><span class="param">s2</span><span class="key"> = </span><span class="key">new </span><span class="method">Set</span><span class="word">(</span><span class="rect">[</span><span class="word">1,1,2,2,2,3,4,5,6</span><span class="rect">]</span><span class="word">);</span><span class="com"> // [1,2,3,4,5,6]</span></pre>
            <p>
                <span class="tip">Set</span> 中有两个个属性。一、<span class="code">Set.prototype.constructor</span>，构造函数，默认就是 <span class="tip">Set</span> 函数；二、<span class="code">Set.prototype.size</span>，用来返回 <span class="tip">Set</span> 的长度。
            </p>
            <pre class="code"><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">s1</span><span class="word">.</span><span class="spe">size</span><span class="word">);</span><span class="com"> // 6</span></pre>
            <p>
                由上面的介绍可以看出，可以利用 <span class="tip">Set</span> 的特性完成数组的去重。
            </p>
            <pre class="code"><span class="key">let </span><span class="param">arr</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,1,3,4,3,5,6,5,2</span><span class="rect">]</span><span class="word">;</span><br><span class="key">let </span><span class="param">unique</span><span class="key"> = </span><span class="rect">[</span><span class="key">...new </span><span class="method">Set</span><span class="word">(</span><span class="param">arr</span><span class="word">)</span><span class="rect">]</span><span class="word">;</span><span class="com"> // [1,2,3,4,5,6]</span></pre>
            <p>
                下面简单介绍一下 <span class="tip">Set</span> 的方法：
            </p>
            <p>
                1、<span class="code">add(value)</span>：向 <span class="tip">Set</span> 添加某 <span class="code">value</span>，返回值为 <span class="tip">Set</span>。（便于链式操作）
            </p>
            <p>
                2、<span class="code">delete(value)</span>：删除 <span class="tip">Set</span> 中某 <span class="code">value</span>，返回值为布尔值，表示删除是否成功。
            </p>
            <p>
                3、<span class="code">has(value)</span>：判断 <span class="tip">Set</span> 中某 <span class="code">value</span> 是否存在，返回布尔值。
            </p>
            <p>
                4、<span class="code">clear()</span>：清空 <span class="tip">Set</span>，没有返回值。
            </p>
            <p>
                <span class="tip">Set</span> 也有遍历操作：
            </p>
            <p>
                1、<span class="code">keys()</span>：返回键名的遍历器。
            </p>
            <p>
                2、<span class="code">values()</span>：返回键值的遍历器。可以用 <span class="code">for(let i of set1){ /* ... */ }</span> 替代。
            </p>
            <p>
                3、<span class="code">entries()</span>：返回键值对的遍历器。
            </p>
            <p>
                4、<span class="code">forEach()</span>：使用回调函数遍历所有成员。
            </p>
            <p>
                此外，数组的 <span class="code">.filter()</span> 和 <span class="code">.map()</span> 都可以在 <span class="tip">Set</span> 中使用。利用这个特性，可以对 <span class="tip">Set</span> 进行一些处理（如：运算、并集、交集、差集等）。下面有一些简单的示例：
            </p>
            <pre class="code"><span class="com">// Set 平方</span><br><span class="key">let</span><span class="param"> s1</span><span class="key"> = </span><span class="key">new</span><span class="method"> Set</span><span class="word">(</span><span class="rect">[</span><span class="word">1,2,3</span><span class="rect">]</span><span class="word">);</span><br><span class="param">s1</span><span class="key"> = </span><span class="key">new</span><span class="method"> Set</span><span class="word">( </span><span class="rect">[</span><span class="key">...</span><span class="param">s1</span><span class="rect">]</span><span class="word">.</span><span class="method">map</span><span class="word">(</span><span class="word"> x</span><span class="key"> => </span><span class="word">x </span><span class="key"> * </span><span class="word">x ) );</span><span class="com"> // [1,4,9] （遍历中不能改变原来的 Set ，需要重新赋值）</span><br><br><span class="key">let </span><span class="param">s2</span><span class="key"> = </span><span class="key">new</span><span class="method"> Set</span><span class="word">(</span><span class="rect">[</span><span class="word">1,2,3</span><span class="rect">]</span><span class="word">);</span><br><span class="key">let </span><span class="param">s3</span><span class="key"> = </span><span class="key">new</span><span class="method"> Set</span><span class="word">(</span><span class="rect">[</span><span class="word">2,3,4,5</span><span class="rect">]</span><span class="word">);</span><br><span class="com">// 并集</span><br><span class="key">let </span><span class="param">union</span><span class="key"> = </span><span class="key">new </span><span class="method">Set</span><span class="word">(</span><span class="rect">[</span><span class="key">...</span><span class="param">s2</span><span class="word">,</span><span class="key">...</span><span class="param">s3</span><span class="rect">]</span><span class="word">);</span><span class="com"> // [1,2,3,4,5]</span><br><span class="com">// 交集</span><br><span class="key">let </span><span class="param">intersect</span><span class="key"> = </span><span class="key">new </span><span class="method">Set</span><span class="word">(</span><span class="rect">[</span><span class="key">...</span><span class="param">s2</span><span class="rect">]</span><span class="word">.</span><span class="method">filter</span><span class="word">( x</span><span class="key"> => </span><span class="param">s3</span><span class="word">.</span><span class="method">has</span><span class="word">(x) ));</span><span class="com"> // [2,3]</span><br><span class="com">// 差集</span><br><span class="key">let </span><span class="param">difference</span><span class="key"> = </span><span class="key">new </span><span class="method">Set</span><span class="word">(</span><span class="rect">[</span><span class="key">...</span><span class="param">s2</span><span class="rect">]</span><span class="word">.</span><span class="method">filter</span><span class="word">( x</span><span class="key"> => !</span><span class="param">s3</span><span class="word">.</span><span class="method">has</span><span class="word">(x) ));</span><span class="com"> // [1] （只取到前面的不同部分）</span></pre>
            <p class="minititle">二、WeakSet</p>
            <p>
                <span class="tip">WeakSet</span> 和 <span class="tip">Set</span> 类似。但是其成员只是是对象。
            </p>
            <p>
                <span class="tip">WeakSet</span> 中的对象都是弱引用，即垃圾回收机制不考虑 <span class="tip">WeakSet</span> 对该对象的引用，也就是说，如果其他对象都不再引用该对象，那么垃圾回收机制会自动回收该对象所占用的内存，不考虑该对象还存在于 <span class="tip">WeakSet</span> 之中。
            </p>
            <p>
                这是因为垃圾回收机制依赖引用计数，如果一个值的引用次数不为 0，垃圾回收机制就不会释放这块内存。结束使用该值之后，有时会忘记取消引用，导致内存无法释放，进而可能会引发内存泄漏。<span class="tip">WeakSet</span> 里面的引用，都不计入垃圾回收机制，所以就不存在这个问题。因此，<span class="tip">WeakSet</span> 适合临时存放一组对象，以及存放跟对象绑定的信息。只要这些对象在外部消失，它在 <span class="tip">WeakSet</span> 里面的引用就会自动消失。
            </p>
            <p>
                由于上面这个特点，<span class="tip">WeakSet</span> 的成员是不适合引用的，因为它会随时消失。另外，由于 <span class="tip">WeakSet</span> 内部有多少个成员，取决于垃圾回收机制有没有运行，运行前后很可能成员个数是不一样的，而垃圾回收机制何时运行是不可预测的，因此 <span class="tip">ES6</span> 规定 <span class="tip">WeakSet</span> 不可遍历。
            </p>
            <p>
                这些特点同样适用于本章后面要介绍的 <span class="tip">WeakMap</span> 结构。【 以上四段摘自 <a href="http://es6.ruanyifeng.com/#docs/set-map" target="_blank">《ECMASCRIPT6 入门 - Set 和 Map 数据结构》</a>】
            </p>
            <pre class="code"><span class="com">// 新建 WeakSet 示例</span><br><span class="key">let </span><span class="param">ws</span><span class="key"> = </span><span class="key">new </span><span class="method">WeakSet</span><span class="word">(</span><span class="rect">[</span><span class="rect">[</span><span class="word">1,2</span><span class="rect">]</span><span class="word">,</span><span class="rect">[</span><span class="word">3,4</span><span class="rect">]]</span><span class="word">);</span><span class="com"> // 如果是传入 [1,2,3,4] 成员就不是对象了，会报错</span></pre>
            <p>
                下面介绍一下 <span class="tip">WeakSet</span> 的方法：
            </p>
            <p>
                1、<span class="code">add(value)</span>：向 <span class="tip">WeakSet</span> 添加某 <span class="code">value</span>，返回值为 <span class="tip">WeakSet</span>。（便于链式操作）
            </p>
            <p>
                2、<span class="code">delete(value)</span>：删除 <span class="tip">WeakSet</span> 中某 <span class="code">value</span>，返回值为布尔值，表示删除是否成功。
            </p>
            <p>
                3、<span class="code">has(value)</span>：判断 <span class="tip">WeakSet</span> 中某 <span class="code">value</span> 是否存在，返回布尔值。
            </p>
            <p>
                由于 <span class="tip">WeakSet</span> 不能遍历（<span class="code">forEach</span> 等方法都不能使用），没有 <span class="code">size</span>，也没有 <span class="code">clear()</span> 方法。
            </p>
            <p class="minititle">三、Map</p>
            <p>
                在以往的 <span class="tip">JavaScript</span> 中，<span class="tip">Object</span> 只能用字符串作为键，在实际使用中会有一些局限。为了解决这个问题，<span class="tip">ES6</span> 提供了 <span class="tip">Map</span> 结构。与 <span class="tip">Object</span> “字符串-值”对应的结构不同，<span class="tip">Map</span> 提供了“值-值”对应。
            </p>
            <p>
                与 <span class="tip">Set</span> 的新建类似。也可以先创建一个空的 <span class="tip">Map</span>，再用 <span class="code">set()</span> 添加成员，或者创建的时候提供参数（二维数组格式）。见下例：
            </p>
            <pre class="code"><span class="com">// 先新建，后添加</span><br><span class="key">const </span><span class="param">m1</span> <span class="key"> = new </span><span class="method">Map</span><span class="word">();</span><br><span class="key">const </span><span class="param">k</span><span class="key"> = </span><span class="flower">{ </span><span class="spe">k</span><span class="word">: </span><span class="str">'key'</span><span class="flower"> }</span><span class="word">;</span><br><span class="key">const </span><span class="param">v</span><span class="key"> = </span><span class="flower">{ </span><span class="spe">v</span><span class="word">: </span><span class="str">'value'</span><span class="flower"> }</span><span class="word">;</span><br><span class="param">m1</span><span class="word">.</span><span class="method">set</span><span class="word">(</span><span class="param">k</span><span class="word">,</span><span class="param">v</span><span class="word">);</span><br><br><span class="com">// 新建时添加</span><br><span class="key">const </span><span class="param">m2</span><span class="key"> = new </span><span class="method">Map</span><span class="word">(</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="str">'k1'</span><span class="word">,</span><span class="str">'v1'</span><span class="rect">]</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="str">'k2'</span><span class="word">,</span><span class="str">'v2'</span><span class="rect">]</span><br><span class="rect">]</span><span class="word">);</span></pre>
            <p>
                实际上，任何具有 <span class="tip">Iterator</span> 接口、且成员均为双元素的数组和数据结构都可以当做 <span class="tip">Map</span> 构造函数的参数。也就是说，<span class="tip">Set</span> 也可以生成 <span class="tip">Map</span>。例如：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">s</span><span class="key"> = new </span><span class="method">Set</span><span class="word">(</span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="str">'k1'</span><span class="word">,</span><span class="str">'v1'</span><span class="rect">]</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="str">'k2'</span><span class="word">,</span><span class="str">'v2'</span><span class="rect">]</span><br><span class="rect">]</span><span class="word">);</span><br><span class="key">const </span><span class="param">m</span><span class="key"> = new </span><span class="method">Map</span><span class="word">(</span><span class="param">s</span><span class="word">);</span></pre>
            <p>
                和 <span class="tip">Object</span> 一样，如果给同一个键赋值，会覆盖之前的值。另外在赋值的时候存在一个问题，一定要注意。见下例（提前介绍一个 <span class="tip">Map</span> 的方法：<span class="code">Map.prototype.get(key)</span>，用于获取 <span class="tip">Map</span> 中的值）：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">map</span><span class="key"> = new </span><span class="method">Map</span><span class="word">();</span><br><span class="param">map</span><span class="word">.</span><span class="method">set</span><span class="word">(</span><span class="flower">{</span><span class="spe">k</span><span class="word">:</span><span class="str">'key'</span><span class="flower">}</span><span class="word">,</span><span class="str">'value'</span><span class="word">);</span><br><span class="param">map</span><span class="word">.</span><span class="method">get</span><span class="word">(</span><span class="flower">{</span><span class="spe">k</span><span class="word">:</span><span class="str">'key'</span><span class="flower">}</span><span class="word">);</span><span class="com"> // undefined</span></pre>
            <p>
                因为只有对同一对象的引用才能取到，上述代码中的两个 <span class="code">{k:'key'}</span> 指向的地址不同。当找不到对应的键的时候，就会返回 <span class="code">undefined</span>。这样做还有一个好处，如果两个键是一样的（<span class="code">{k:'key'}</span>），只要保存的变量不同，就不会产生同名属性碰撞。
            </p>
            <p>
                <span class="tip">Map</span> 判断是否是一个键的规则如下：一个简单类型的值（数字、字符串、布尔值），则只要两个值严格相等（<span class="code">===</span>），Map 将其视为一个键。比较特殊的是： <span class="code">NaN</span> 视为同一个键，<span class="code">undefined</span> 和 <span class="code">null</span> 是不同的键。
            </p>
            <p>
                 <span class="tip">Map</span> 中也有 <span class="code">size</span> 属性，表示长度。下面简单介绍一下 <span class="tip">Map</span> 的方法：
            </p>
            <p>
                1、<span class="code">set(key,value)</span>：向 <span class="tip">Map</span> 添加某键值对 <span class="code">(key,value)</span>，返回值为 <span class="tip">Map</span>。（便于链式操作）
            </p>
            <p>
                2、<span class="code">get(key)</span>：获得 <span class="tip">Map</span> 中某 <span class="code">key</span> 对应的 <span class="code">value</span>，返回值为 <span class="code">value</span>。
            </p>
            <p>
                3、<span class="code">delete(key)</span>：删除 <span class="tip">Map</span> 中某 <span class="code">key</span>，返回值为布尔值，表示删除是否成功。
            </p>
            <p>
                4、<span class="code">has(key)</span>：判断 <span class="tip">Map</span> 中某 <span class="code">key</span> 是否存在，返回布尔值。
            </p>
            <p>
                5、<span class="code">clear()</span>：清空 <span class="tip">Map</span>，没有返回值。
            </p>
            <p>
                <span class="tip">Map</span> 的遍历操作（和 <span class="tip">Set</span> 一样）：
            </p>
            <p>
                1、<span class="code">keys()</span>：返回键名的遍历器。
            </p>
            <p>
                2、<span class="code">values()</span>：返回键值的遍历器。可以用 <span class="code">for(let i of set1){ /* ... */ }</span> 替代。
            </p>
            <p>
                3、<span class="code">entries()</span>：返回键值对的遍历器。
            </p>
            <p>
                4、<span class="code">forEach()</span>：使用回调函数遍历所有成员。
            </p>
            <p>
                <span class="tip">Map</span> 可以转换成其他的各种结构。具体的转换不在此介绍，根据上面的特性喝方法都不难完成。
            </p>
            <p class="minititle">四、WeakMap</p>
            <p>
                <span class="tip">WeakMap</span> 只接受对象作为键名（除了 <span class="code">null</span>）。<span class="tip">WeakMap</span> 的键名所指向的对象，不计入垃圾回收机制。
            </p>
            <p>
                <span class="tip">WeakMap</span> 设计目的：在某些情况下，在某对象上存放数据会形成对这个对象的引用。见下例：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">e1</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'element1'</span><span class="word">);</span><br><span class="key">const </span><span class="param">e2</span><span class="key"> = </span><span class="spe">document</span><span class="word">.</span><span class="method">getElementById</span><span class="word">(</span><span class="str">'element2'</span><span class="word">);</span><br><span class="key">const </span><span class="param">arr</span><span class="key"> = </span><span class="rect">[</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="param">e1</span><span class="word">,</span><span class="str">'element1 元素'</span><span class="rect">]</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="rect">[</span><span class="param">e2</span><span class="word">,</span><span class="str">'element2 元素'</span><span class="rect">]</span><br><span class="rect">]</span><span class="word">;</span></pre>
            <p>
                在上面的例子中，为两个对象做了文字说明，从而形成了 <span class="code">arr</span> 对 <span class="code">e1</span> 和 <span class="code">e2</span> 的引用。如果不再需要的话，则需要手动删除该引用，否则垃圾回收机制就不会释放 <span class="code">e1</span> 和 <span class="code">e2</span> 的内存。
            </p>
            <p>
                如果要释放就要写下面的代码：
            </p>
            <pre class="code"><span class="param">arr</span><span class="rect">[</span><span class="word">0</span><span class="rect">]</span><span class="key"> = </span><span class="spe">null</span><span class="word">;</span><br><span class="param">arr</span><span class="rect">[</span><span class="word">1</span><span class="rect">]</span><span class="key"> = </span><span class="spe">null</span><span class="word">;</span></pre>
            <p>
                这样的做法很不方便，忘记写就会造成内存泄露；万一不小心写错地方，后面就没了。
            </p>
            <p>
                <span class="tip">WeakMap</span> 的存在就是为了应对这种场景的。它的键名所引用的对象都是弱引用，垃圾回收机制不会考虑该引用。因此，只要所引用的对象的其他引用都被清除，就会释放该对象所占用的内存。也就是说，一旦不再需要，<span class="tip">WeakMap</span> 里面的键名对象和所对应的键值对会自动消失，不用手动删除引用。
            </p>
            <p>
                由于 <span class="tip">WeakMap</span> 的特点，它没有 <span class="code">size</span> 属性，也没有遍历操作（即没有 <span class="code">key()</span>、<span class="code">values()</span>、<span class="code">entries()</span> 方法）。能使用的方法只有 <span class="code">get()</span>、<span class="code">set()</span>、<span class="code">has()</span>、<span class="code">delete()</span>。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


