<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、原理</p>
            <p>
                首先看一个例子：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">s1</span><span class="key"> = </span><span class="str">'new string'</span><span class="word">;</span><br><span class="key">var </span><span class="param">s2</span><span class="key"> = </span><span class="param">s1</span><span class="word">.</span><span class="method">substring</span><span class="word">(2);</span></pre>
            <p>
                基本类型值不是对象，从逻辑上讲不应该有方法（实际是有的）。为了实现这种直观的操作，后台自动完成的一系列的处理，第二行代码访问 <span class="code">s1</span> 的时候，访问过程处于一种读取模式，也就是从内存中读取这个字符串的值。在读取过程中，后台自动完成了下列处理：
            </p>
            <p>
                1、创建 <span class="tip">String</span> 类型的一个实例；
            </p>
            <p>
                2、在实例上调用指定的方法；
            </p>
            <p>
                3、销毁这个实例。
            </p>
            <p>
                这三步操作相当于（经过这样的处理，就把基本的类型转化为了对象。也同样适用于 <span class="tip">Boolean</span> 和 <span class="tip">Number</span>）：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">s1</span><span class="key"> = </span><span class="key">new </span><span class="fun">String</span><span class="word">(</span><span class="str">'new string'</span><span class="word">);</span><br><span class="key">var </span><span class="param">s2</span><span class="key"> = </span><span class="param">s1</span><span class="word">.</span><span class="method">substring</span><span class="word">(2);</span><br><span class="param">s1</span><span class="key"> = null</span><span class="word">;</span></pre>
            <p>
                引用类型和基本包装类型的主要区别就是对象的生存期。使用 <span class="code">New</span> 操作符创建的引用类型的实例，在执行流离开当前作用域之前都保存在内存中，而自动创建的基本包装类型的对象，只存在于执行代码的瞬间，之后就会被销毁。也就是说，不能在运行时为基本类型添加属性和方法。
            </p>
            <p class="minititle">二、Object 构造函数</p>
            <p>
                <span class="tip">Object</span> 构造函数会像工厂方法一样，根据传入值的类型返回相应包装类型的实例。例如：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">obj</span><span class="key"> = </span><span class="key">new </span><span class="method">Object</span><span class="word">(</span><span class="str">'some text'</span><span class="word">);</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">obj</span><span class="key"> instanceof </span><span class="method">String</span><span class="word">);</span><span class="com"> // true</span></pre>
            <p>
                上例中，将字符串传给 <span class="code">Object</span> 构造函数，就创建了 <span class="code">String</span> 的实例（传入什么类型就会得到什么类型的实例）。而直接调用转型函数不同。例如： <span class="code"> console.log( typeof Number('25') )</span>（结果是 <span class="tip">Number</span> 类型）。如果是特定类型的构造函数则是：<span class="code"> console.log( typeof new Number('25') )</span>（结果是 <span class="tip">Object</span> 类型，保存的是 <span class="tip">Number</span> 的实例对象）。
            </p>
            <p class="minititle">三、Boolean 类型</p>
            <p>
                <span class="tip">Boolean</span> 对象在 <span class="tip">ECMAScript</span> 中用处不大，而且会造成误解。例如：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">fObject</span><span class="key"> = new </span><span class="method">Boolean</span><span class="word">(</span><span class="key">false</span><span class="word">);</span><br><span class="key">var </span><span class="param">fValue</span><span class="key"> = </span><span class="key">false</span><span class="word">;</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">fObject</span><span class="key"> && true</span><span class="word">);</span><span class="com"> // true</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">fValue</span><span class="key"> && true</span><span class="word">);</span><span class="com"> // false</span></pre>
            <p>
                因为第一个是作为对象（<span class="tip">Object</span>）去判断的，所以会被认为是 <span class="code">true</span>，而后面的 <span class="code">fValue</span> 才是真正的 <span class="code">false</span>。只要能理解它们的区别就好了，不建议使用 <span class="tip">Boolean</span> 对象。
            </p>
            <p class="minititle">四、Number 类型</p>
            <p>
                与 <span class="tip">Boolean</span> 类型一样，<span class="tip">Number</span> 也重写了 <span class="code">valueOf()</span>、<span class="code">toLocaleString()</span>、<span class="code">toString()</span> 方法。
            </p>
            <p>
                <span class="code">valueOf()</span> 返回对象表示的基本类型的数值，另外两个方法返回字符串形式的数值。<span class="code">toString()</span> 还能返回数值的字符串形式，例如：
            </p>
            <pre class="code"><span class="key">var </span><span class="param">num</span><span class="key"> = </span><span class="word">10;</span><br><span class="param">num</span><span class="word">.</span><span class="method">toString</span><span class="word">();</span><span class="com">&nbsp;&nbsp;&nbsp;// "10" </span><br><span class="param">num</span><span class="word">.</span><span class="method">toString</span><span class="word">(2);</span><span class="com">&nbsp;&nbsp;// "2" </span><br><span class="param">num</span><span class="word">.</span><span class="method">toString</span><span class="word">(8);</span><span class="com">&nbsp;&nbsp;// "8" </span><br><span class="param">num</span><span class="word">.</span><span class="method">toString</span><span class="word">(10);</span><span class="com">&nbsp;// "10" </span><br><span class="param">num</span><span class="word">.</span><span class="method">toString</span><span class="word">(16);</span><span class="com">&nbsp;// "a" </span><br></pre>
            <p>
                <span class="code">toFixed()</span> 方法有四舍五入的功能，很适合用于处理货币，在 <span class="tip">IE8</span> 及更早的版本不能正确识别 <span class="tip">{(-0.94,-0.5),(0.5,0.94)}</span> 区间内的值（统一返回 0 ，而不是 -1 或 1）。<span class="tip">IE9</span> 修复了这个问题。<span class="code">toFixed()</span> 方法的标准是可以保留到 <span class="tip">20</span> 位，但有些浏览器可能会支持更多位数。
            </p>
            <p>
                <span class="code">toExponential()</span> 方法是将数值转换为科学计数法，如 <span class="code">(12).toExponential()</span> 得到的结果是  <span class="code">1.2e+1</span>。但是这个方法将所有值都转化为科学计数法，如 <span class="code">12</span> 这样的小数字没有必要转为科学计数法。这时候不需要使用 <span class="code">if</span> 判断，因为还有 <span class="code">toPrecision()</span> 方法下面介绍一下这种方法。
            </p>
            <p>
                <span class="code">toPrecision()</span> 会根据传入的参数去识别保留位数，还是转为科学计数法。如：
            </p>
            <pre class="code"><span class="word">(12).</span><span class="method">toPrecision</span><span class="word">(3);</span><span class="com">&nbsp;&nbsp;&nbsp;// "12.0"</span><br><span class="word">(120).</span><span class="method">toPrecision</span><span class="word">(3);</span><span class="com">&nbsp;&nbsp;// "120"</span><br><span class="word">(1200).</span><span class="method">toPrecision</span><span class="word">(3);</span><span class="com">&nbsp;// "1.20e+3"</span></pre>
            <p>
                与 <span class="tip">Boolean</span> 一样，使用 <span class="code">new Number()</span> 创建的数组类型是 <span class="code">“object”</span>。
            </p>
            <p class="minititle">五、String 类型</p>

        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


