<template>
    <main id="blogList">
        <section class="list-left">
            <my-blog v-for  = "item in $store.state.currentBlogData"
                     :title = "item.title"
                     :url   = "item.url"
                     :key   = "item.url"
                     :time  = "item.time"
                     :tags  = "item.tags"
                     :brief = "item.brief"></my-blog>
                     
            <footer class="text-center">
                <el-pagination
                        @size-change="changeSize"
                        @current-change="changePage"
                        :current-page.sync="currentPage"
                        :page-sizes="[6, 10, 20]"
                        :page-size="$store.state.pageSize"
                        layout="total, prev, pager, next"
                        :total="$store.state.totalBlogLength">
                </el-pagination>
            </footer>
        </section>
        <section class="list-right" v-if="0">
            <div class="chosen">
                <div class="classify">
                    <p class="title">分类</p>
                    标签可供筛选
                </div>
                <div class="latest">
                    <p class="title">最新博客</p>
                    显示最新的10篇博客的标题，分类，时间，标题限制长度
                    <p class="content">new Date() 兼容问题 2016-9-13 javascript</p>
                </div>
            </div>
        </section>
    </main>
</template>
<script>
    export default {
        data: function (){
            return {
                currentPage: 1
            }
        },
        methods: {
            // 换一页
            changePage(val){
                this.currentPage = val;
                this.getData();
            },
            // 改变每页数量
            changeSize(val){
                this.pageSize = val;
                this.getData();
            },
            // PC端 获取数据
            getData(){
                // 根据 pageSize 和 currentPage 获得当前显示的数据
                const that = this;
                const params = {
                    type: 0, // 0 blog , 1 demo
                    currentPage: that.currentPage
                };
                that.$store.commit( 'pageChange', params );          
            },
            // 手机端 获取数据
            getDataInMobile(){
                const that = this;
                const params = {
                    type: 0,
                    currentPage: that.currentPage
                };
                that.$store.commit( 'loadNextPage', params );
            },
            // 滚动到底部
            scrollToFooter(){

            }
        },
        mounted(){
            if(location.hash ==='#/blog') document.title = '个人博客 by 龙 - blogList';
        }
    }
</script>


