<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">学习 ES6 的原因</p>
            <p>
                最近要使用 <span class="tip">vue</span> 开发一个项目。很多插件都需要自己去封装，如分页、 tab 切换等。于是去找了相关的 <span class="tip">UI</span> 框架。<span class="tip">elementUI</span>，还有一个 <span class="tip">iview</span> 都不错。个人更喜欢用 <span class="tip">elementUI</span> 一点。
            </p>
            <p>
                <span class="tip">elementUI</span> 的官方文档是用 <span class="tip">ES6</span> 写的，转换成 <span class="tip">ES5</span> 再开发则会有诸多不便。加上 <span class="tip">ES6</span> 普及已经是大势所趋。抓紧学习有利无弊。于是，就同时踏上了 学习 <span class="tip">ES6</span>  的道路。
            </p>
            <p class="minititle">推荐</p>
            <p>
                这里推荐一下：<a target="_blank" href="http://es6.ruanyifeng.com/">《ECMAScript 6 入门》 - 阮一峰</a>。挺适合入门看的。我最近也在看这本书。
            </p>
            <p class="minititle">let、const、var</p>
            <p>
                <span class="code">let</span> 和 <span class="code">const</span> 是 <span class="tip">ES6</span> 新增的。<span class="code">let</span> 用于定义变量，与 <span class="code">var</span> 很相似，但是更加严谨。<span class="code">const</span> 用于定义常量，且不可修改。
            </p>
            <p>
                <span class="code">let</span> 的严谨主要在于：一、不能重复声明同一个变量；二、只能在声明的代码块中使用。
            </p>
            <p>
                <span class="code">const</span> 除了值是常量不可修改外，和 <span class="code">let</span> 是一样的。
            </p>
            <p>
                简单举一个例子，说明一下 <span class="code">let</span> 比 <span class="code">var</span> 更严谨的地方：
            </p>
            <pre class="code"><span class="com">// ES5 var </span><br><span class="key">var </span><span class="param">a</span><span class="key"> = </span><span class="rect">[]</span><span class="word">;</span><br><span class="key">for</span><span class="word">(</span><span class="key">var </span><span class="param">i</span><span class="key"> = </span><span class="word">0; </span><span class="param">i</span><span class="key"> &lt; </span><span class="word">10; </span><span class="param">i</span><span class="key">++ </span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">a</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> = </span><span class="key">function</span><span class="word"> () </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">i</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">;</span><br><span class="flower">}</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( </span><span class="param">a</span><span class="rect">[</span><span class="word">6</span><span class="rect">]</span><span class="word">() );</span><span class="com"> // 10</span><br><br><span class="com">// ES6 let </span><br><span class="key">var </span><span class="param">a</span><span class="key"> = </span><span class="rect">[]</span><span class="word">;</span><br><span class="key">for</span><span class="word">(</span><span class="key">let </span><span class="param">i</span><span class="key"> = </span><span class="word">0; </span><span class="param">i</span><span class="key"> &lt; </span><span class="word">10; </span><span class="param">i</span><span class="key">++ </span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">a</span><span class="rect">[</span><span class="param">i</span><span class="rect">]</span><span class="key"> = </span><span class="key">function</span><span class="word"> () </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">i</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">;</span><br><span class="flower">}</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( </span><span class="param">a</span><span class="rect">[</span><span class="word">6</span><span class="rect">]</span><span class="word">() );</span><span class="com"> // 6</span></pre>
            <p>
                目前主流的浏览器均支持 <span class="code">let</span> 和 <span class="code">const</span>。使用这两个关键字代码会使代码更加严谨，并且可以避免一些定义变量时存在的问题。
            </p>
            <p class="minititle">解构赋值</p>
            <p>
                解构赋值有点类似于模式匹配。只要两边的结构一致就可以赋值。
            </p>
            <p>
                更复杂的情况也是一样的，其中可以使用 <span class="code">...</span> 运算符。如果解构不成功，会赋值为 <span class="code">undefined</span>。解构赋值还可以指定默认值。示例如下：
            </p>
            <pre class="code"><span class="key">let </span><span class="rect">[</span><span class="param">a</span><span class="word">,</span><span class="param">b</span><span class="word">,</span><span class="param">c</span><span class="rect">]</span><span class="key"> = </span><span class="rect">[</span><span class="word">1,2,3</span><span class="rect">]</span><span class="word">;</span><span class="com"> // a=1,b=2,c=3</span><br><span class="key">let </span><span class="rect">[[</span><span class="param">aa</span><span class="rect">]</span><span class="word">,</span><span class="param">bb</span><span class="key"> = </span><span class="word">8,</span><span class="param">cc</span><span class="rect">]</span><span class="key"> = </span><span class="rect">[[</span><span class="word">1</span><span class="rect">]</span><span class="word">,,3</span><span class="rect">]</span><span class="word">;</span><span class="com">// aa=1,bb=8,cc=3；若bb不指定默认值则为 undefined</span></pre>
            <p>
                如果后面赋值的类型比较特殊，见下例：
            </p>
            <pre class="code"><span class="key">let </span><span class="rect">[</span><span class="param">x</span><span class="key"> = </span><span class="word">1</span><span class="rect">]</span><span class="key"> = </span><span class="rect">[</span><span class="spe">undefined</span><span class="rect">]</span><span class="word">;</span><span class="com"> // x = 1</span><br><span class="key">let </span><span class="rect">[</span><span class="param">y</span><span class="key"> = </span><span class="word">1</span><span class="rect">]</span><span class="key"> = </span><span class="rect">[</span><span class="spe">null</span><span class="rect">]</span><span class="word">;</span><span class="com"> // x = null</span></pre>
            <p>
                由上例可以看出，赋值为 <span class="code">undefined</span> 时，赋值失败，其他情况下会成功赋值。执行原理如下：
            </p>
            <pre class="code"><span class="key">if</span><span class="word">(</span><span class="param">rightValue</span><span class="key"> === </span><span class="spe">undefined</span><span class="word">)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// 执行左边的表达式 leftValue = someValue</span><br><span class="flower">}</span><span class="key">else</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="param">leftValue</span><span class="key"> = </span><span class="spe">undefined</span><span class="word">;</span><br><span class="flower">}</span></pre>
            <p>
                解构赋值不仅可以在数组中使用，也可以在对象中使用，用法基本相同。有一个比较大区别：数组是按顺序对应赋值的；而对象没有顺序，需要同名才能够成功赋值。
            </p>
            <p>
                当然，字符串也可以解构赋值。如：<span class="code">const [a, b, c, d, e] = 'hello'</span>，<span class="code">let {length : len} = 'hello'</span>。函数、布尔值等的解构赋值就不介绍了。
            </p>
            <p class="minititle">箭头函数</p>
            <p>
                箭头函数给人的第一感觉就是简单了很多，与此同时，它与常规的定义函数相比也有一定的区别。
            </p>
            <p>
                一、不能 <span class="code">new</span>；二、不能使用 <span class="tip">Generator</span> 函数；三、<span class="code">this</span> 指向固定（自身无 <span class="code">this</span>，使用上面的 <span class="code">this</span>），不会出现指向问题。
            </p>
            <p>
                其他关于箭头函数的内容在此不做介绍了。有兴趣的，可以去搜索一下关于它的资料。
            </p>
            <p class="minititle">ES6 转 ES5</p>
            <p>
                <span class="tip">ES6</span> 虽然已经出来很久，并且也在开发中大量使用了。但实际上，各大浏览器对 <span class="tip">ES6</span> 并没有做到完全支持。所以使用的时候还需要转换成 <span class="tip">ES5</span>。
            </p>
            <p>
                如果使用了 <span class="tip">webpack</span> 就不需要了，<span class="tip">build</span> 的时候自己会转换过来。
            </p>
            <p>
                下面介绍的转换工具仍然是 <span class="tip">Grunt</span>。没有使用过的话，可以看之前的一篇安装使用的介绍：<a target="_blank" href="/#/blog/blog_33">《Grunt 的安装、配置以及使用》</a>
            </p>
            <p>
                下面的内容是在已经可以使用 <span class="tip">Grunt</span> 的基础上写的。在配置文件 <span class="tip">Gruntfile.js</span> 中添加代码：
            </p>
            <pre class="code"><span class="spe">grunt</span><span class="word">.</span><span class="spe">initConfig</span><span class="word">(</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="com">// ES6 转换</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">babel</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">options</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">sourceMap</span><span class="word">: </span><span class="key">false</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">presets</span><span class="word">: </span><span class="rect">[</span><span class="str">'D:/Grunt/node_modules/babel-preset-es2015'</span><span class="rect">]</span><span class="com"> // 写自己安装这个组件的地址</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">dist</span><span class="word">: </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">files</span><span class="word">:</span><span class="rect">[</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">expand</span><span class="word">: </span><span class="key">true</span><span class="word">,</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">cwd</span><span class="word">: </span><span class="str">'D:/source/'</span><span class="word">,</span><span class="com"> // 带转换源文件目录</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">src</span><span class="word">: </span><span class="str">'**/*.js'</span><span class="word">,</span><span class="com"> // 源文件名称（ * 表示所有）</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">dest</span><span class="word">: </span><span class="str">'D:/source/output/'</span><span class="com"> // 放置输出文件的目录</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="rect">]</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><span class="word">);</span><br><span class="spe">grunt</span><span class="word">.</span><span class="method">loadNpmTasks</span><span class="word">(</span><span class="str">'grunt-babel'</span><span class="word">);</span><br><span class="spe">grunt</span><span class="word">.</span><span class="method">registerTask</span><span class="word">(</span><span class="str">'toes5'</span><span class="word">,</span><span class="rect">[</span><span class="str">'babel'</span><span class="rect">]</span><span class="word">);</span></pre>
            <p>
                只是这样还不能使用，还要安装这些组件。（这些组件有多，也不知道是不是下了多余的了）
            </p>
            <pre class="cmd">npm install grunt-babel --save-dev</pre>
            <p>
                安装这个以后，会提示找不到 <span class="tip">babel-preset-es2015</span>，那就把这个组件也安装了。
            </p>
            <pre class="cmd">npm install babel-preset-es2015</pre>
            <p>
                这样一来只要路径对了，就可以正常使用了。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


