<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、函数内部属性（arguments 和 this）</p>
            <p>
                函数内部有两个特殊的对象 <span class="code">this</span> 和 <span class="code">arguments</span>。<span class="code">arguments</span> 除了保存函数参数外，还有一个 <span class="code">callee</span> 的属性，这个属性是一个指针，指向拥有这个 <span class="code">arguments</span> 对象的函数。下面是一个 <span class="code">callee</span> 的作用示例：  
            </p>
            <pre class="code"><span class="key">function </span><span class="fun">factorial</span><span class="word">(num)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( num</span><span class="key"> &lt;= </span><span class="word">1 )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key"> else </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">num </span><span class="key">* </span><span class="fun">factorial</span><span class="word">(num - 1);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span></pre>
            <p>
                上例的阶乘递归函数中，用到了 <span class="code">factorial</span> 的函数名，内部与外部的变量名存在高耦合，如将外部函数改名会影响到内部，导致两边名字需要同时修改。而使用 <span class="code">arguments.callee</span> 就不存在这种情况了（在严格模式下，不能访问 <span class="code">arguments.callee</span> 属性，这是为了保证 <span class="tip">JavaScript</span> 的安全性，防止第三方代码在相同环境下窥视其他代码）。
            </p>
            <pre class="code"><span class="key">function </span><span class="fun">factorial</span><span class="word">(num)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">if</span><span class="word">( num</span><span class="key"> &lt;= </span><span class="word">1 )</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">1;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><span class="key"> else </span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">num </span><span class="key">* </span><span class="spe">arguments</span><span class="word">.</span><span class="spe">callee</span><span class="word">(num - 1);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span></pre>
            <p>
                另一个参数 <span class="code">this</span> 指向的是函数当前的执行环境。如果当前的执行环境是全局，那么 <span class="code">this</span> 指向的就是 <span class="code">window</span> 对象。<span class="code">this</span> 属性在此不展开介绍。
            </p>
            <p>二、函数的属性</p>
            <p>
                函数有两个属性：<span class="code">length</span> 和 <span class="code">prototype</span>。<span class="code">length</span> 是函数参数的数量。<span class="code">prototype</span> 保存了函数所有的实例方法。在 <span class="tip">ECMAScript 5</span> 中，<span class="code">prototype</span> 属性是不可枚举的。
            </p>
            <p>
                每一个函数都包含了两个非继承而来的方法：<span class="code">call()</span> 和 <span class="code">apply()</span>。<span class="code">call()</span> 和 <span class="code">apply()</span> 作用相同，它们的区别仅仅是接收参数的方式不同（如果不传参数的话，用哪个都一样）。见下面一个例子：
            </p>
            <pre class="code"><span class="key">function </span><span class="fun">a</span><span class="word">(aa,bb)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="word">aa</span><span class="key"> + </span><span class="word">bb;</span><br><span class="flower">}</span><br><span class="key">function </span><span class="fun">b</span><span class="word">(aa,bb)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="fun">a</span><span class="word">.</span><span class="method">apply</span><span class="word">(</span><span class="key">this</span><span class="word">,</span><span class="spe">arguments</span><span class="word">);</span><span class="com"> // 或者 用 [aa,bb]</span><br><span class="flower">}</span><br><span class="key">function </span><span class="fun">c</span><span class="word">(aa,bb)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">return </span><span class="fun">a</span><span class="word">.</span><span class="method">call</span><span class="word">(</span><span class="key">this</span><span class="word">,</span><span class="word">aa,bb</span><span class="word">);</span><span class="com"> // 需要展开参数，或者用 es6 展开符：...arguments</span><br><span class="flower">}</span></pre>
            <p>
                上面展示了 <span class="code">call()</span> 和 <span class="code">apply()</span> 的用法，但实际上它们最大的作用并非在传参上。它们真正的强大的地方是能够扩充函数赖以运行的作用域。下面看一个例子：
            </p>
            <pre class="code"><span class="spe">window</span><span class="word">.</span><span class="spe">color</span><span class="key"> = </span><span class="str">'red'</span><span class="word">;</span><br><span class="key">var </span><span class="param">obj</span><span class="key"> = </span><span class="flower">{</span><span class="spe"> color</span><span class="word">: </span><span class="str">'blue'</span><span class="flower"> }</span><span class="word">;</span><br><span class="key">function </span><span class="fun">getColor</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( </span><span class="key">this</span><span class="word">.</span><span class="spe">color</span><span class="word"> );</span><br><span class="flower">}</span><br><span class="fun">getColor</span><span class="word">();</span><span class="com"> // red</span><br><span class="fun">getColor</span><span class="word">(</span><span class="key">this</span><span class="word">);</span><span class="com"> // red；这里传参是没用的，原函数没有接受参数</span><br><br><span class="fun">getColor</span><span class="word">.</span><span class="method">call</span><span class="word">(</span><span class="key">this</span><span class="word">);</span><span class="com"> // red</span><br><span class="fun">getColor</span><span class="word">.</span><span class="method">call</span><span class="word">(</span><span class="spe">window</span><span class="word">);</span><span class="com"> // red</span><br><span class="fun">getColor</span><span class="word">.</span><span class="method">call</span><span class="word">(</span><span class="param">obj</span><span class="word">);</span><span class="com"> // blue</span></pre>
            <p>
                从上面的例子可以看出，使用 <span class="code">call()</span> 方法可以改变函数作用域，并且不需要传递参数。这样对象和方法就解耦了（如果要使上例使用 <span class="code">console.log(this.color)</span> 输出 <span class="code">blue</span> 的而不适用 <span class="code">call()</span> 或 <span class="code">apply()</span> 的话，则需要将方法放到 <span class="code">obj</span> 对象中才行）。
            </p>
            <p>
                还有一个 <span class="code">bind()</span> 方法。用来创建一个函数实例，绑定 <span class="code">this</span> 值到传入的参数中去（之后调用就不用再绑定了，比较灵活。另外：支持<span class="code">bind()</span> 方法的浏览器有 <span class="tip">IE9+</span>、<span class="tip">FireFox4+</span>、<span class="tip">Safari5.1+</span>、<span class="tip">Opera12+</span> 和 <span class="tip">Chrome</span> ）。见下例：
            </p>
            <pre class="code"><span class="spe">window</span><span class="word">.</span><span class="spe">color</span><span class="key"> = </span><span class="str">'red'</span><span class="word">;</span><br><span class="key">var </span><span class="param">obj</span><span class="key"> = </span><span class="flower">{</span><span class="spe"> color</span><span class="word">: </span><span class="str">'blue'</span><span class="flower"> }</span><span class="word">;</span><br><span class="key">function </span><span class="fun">getColor</span><span class="word">()</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">( </span><span class="key">this</span><span class="word">.</span><span class="spe">color</span><span class="word"> );</span><br><span class="flower">}</span><br><span class="key">var </span><span class="param">getObjColor</span><span class="key"> = </span><span class="fun">getColor</span><span class="word">.</span><span class="method">bind</span><span class="word">(</span><span class="param">obj</span><span class="word">);</span><br><span class="fun">getObjColor</span><span class="word">();</span><span class="com"> // blue</span></pre>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


