<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                <span class="tip">Proxy</span> 构造函数生成实例的方法如下：
            </p>
            <pre class="code"><span class="com">/* target：要拦截的目标对象，handler：定制拦截行为 */</span><br><span class="key">var </span><span class="param">proxy</span><span class="key"> = new </span><span class="method">Proxy</span><span class="word">(target,handler);</span></pre>
            <p>
                首先介绍一下 <span class="tip">Proxy</span> 支持的拦截操作（需要一些对 <span class="tip">Object</span> 操作的了解）：
            </p>
            <el-table :data="table_104">
                <el-table-column prop="name" label="操作名">
                    <template scope="scope">
                        <span v-html="scope.row.name">{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="intro" label="说明">
                    <template scope="scope">
                        <span v-html="scope.row.intro">{{scope.row.intro}}</span>
                    </template>
                </el-table-column>
            </el-table>
            <p>
                下面是一个一个
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


