<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p class="minititle">一、基础用法</p>
            <p>
                先引入一个例子：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">a</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">();</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">a</span><span class="word">);</span><span class="com"> // Symbol()</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="method">typeof </span><span class="param">a</span><span class="word">);</span><span class="com"> // "symbol"</span></pre>
            <p>
                这个 <span class="code">a</span> 有点像一个记号，表示 <span class="code">a</span> 是独一无二的值。如果该变量名语义明确则可以做一些其他事情。例如：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">triangle</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">();</span><br><span class="key">function </span><span class="fun">checkShape</span><span class="word">(shape)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">switch</span><span class="word">(shape)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">case </span><span class="param">triangle</span><span class="word">:</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="str">'This is triangle'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">break</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><br><span class="fun">checkShape</span><span class="word">(</span><span class="param">triangle</span><span class="word">);</span></pre>
            <p>
                在 <span class="tip">ES5</span> 中，要实现上面的效果，代码如下：
            </p>
            <pre class="code"><span class="key">function </span><span class="fun">checkShape</span><span class="word">(shape)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">switch</span><span class="word">(shape)</span><span class="flower">{</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">case </span><span class="str">'triangle'</span><span class="word">:</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="str">'This is triangle'</span><span class="word">);</span><br>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span class="key">break</span><span class="word">;</span><br>&nbsp;&nbsp;&nbsp;&nbsp;<span class="flower">}</span><br><span class="flower">}</span><br><br><span class="fun">checkShape</span><span class="word">(</span><span class="str">'triangle'</span><span class="word">);</span></pre>
            <p>
                上面的代码中，少定义了一个变量，<span class="code">'triangle'</span> 属于魔术字符串，这个值的存在会增强代码的耦合度，不利于将来的修改和维护。消除魔术字符串的方法就是把它定义为一个变量。<span class="code">var triangle = 'triangle'</span>。这时候只需要去对比变量即可。
            </p>
            <p>
                在这种场景下，<span class="code">triangle</span> 这个变量不会做改动，也不会有重复的特定。正好非常符合 <span class="tip">Symbol</span> 的定义，我们并不需要关心这个变量的值是什么，只需要知道现在是它就足够了。如此一来，就可以定义为 <span class="code">triangle</span> 为 <span class="tip">Symbol</span> 类型的变量。即：<span class="code">const triangle = Symbol()</span>。
            </p>
            <p>
                <span class="tip">Symbol()</span> 也可以带参数使用。例：
            </p>
            <pre class="code"><span class="key">const </span><span class="param">obj</span><span class="key"> = </span><span class="flower">{}</span><span class="word">;</span><br><span class="key">let </span><span class="param">a</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">(</span><span class="str">'a'</span><span class="word">);</span><br><span class="key">let </span><span class="param">b</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">(</span><span class="str">'b'</span><span class="word">);</span><br><span class="param">obj</span><span class="rect">[</span><span class="param">a</span><span class="rect">]</span><span class="key"> = </span><span class="str">'Hello'</span><span class="word">;</span><br><span class="param">obj</span><span class="rect">[</span><span class="param">b</span><span class="rect">]</span><span class="key"> = </span><span class="str">'World'</span><span class="word">;</span><br><span class="key">const </span><span class="param">objectSymbols</span><span class="key"> = </span><span class="spe">Object</span><span class="word">.</span><span class="method">getOwnPropertySymbols</span><span class="word">(</span><span class="param">obj</span><span class="word">);</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">objectSymbols</span><span class="word">);</span><span class="com"> // [Symbol(a),Symbol(b)]</span></pre>
            <p class="minititle">二、Symbol.for() 和 Symbol.keyFor()</p>
            <p>
                如果希望重新使用同一个 <span class="tip">Symbol</span> 值，需要使用 <span class="code">Symbol.for()</span> 方法。相当于是利用此方法给 <span class="tip">Symbol</span> 登记了一个值。
            </p>
            <pre class="code"><span class="key">let</span><span class="param"> s1</span><span class="key"> = </span><span class="spe">Symbol</span><span class="word">.</span><span class="method">for</span><span class="word">(</span><span class="str">'symbol1'</span><span class="word">);</span><br><span class="key">let</span><span class="param"> s2</span><span class="key"> = </span><span class="spe">Symbol</span><span class="word">.</span><span class="method">for</span><span class="word">(</span><span class="str">'symbol1'</span><span class="word">);</span><br><span class="key">let</span><span class="param"> s3</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">(</span><span class="str">'symbol2'</span><span class="word">);</span><br><span class="key">let</span><span class="param"> s4</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">(</span><span class="str">'symbol2'</span><span class="word">);</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">s1</span><span class="key"> === </span><span class="param">s2</span><span class="word">);</span><span class="com"> // true</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="param">s3</span><span class="key"> === </span><span class="param">s4</span><span class="word">);</span><span class="com"> // false</span><br></pre>
            <p>
                <span class="code">Symbol.keyFor()</span> 方法可以返回一个已登记的 <span class="tip">Symbol</span> 类型值的 <span class="tip">key</span>。例：
            </p>
            <pre class="code"><span class="key">let </span><span class="param">s1</span><span class="key"> = </span><span class="spe">Symbol</span><span class="word">.</span><span class="method">for</span><span class="word">(</span><span class="str">'symbol1'</span><span class="word">);</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="spe">Symbol</span><span class="word">.</span><span class="method">keyFor</span><span class="word">(</span><span class="param">s1</span><span class="word">);</span><span class="com"> // 'symbol1'</span><br><span class="key">let </span><span class="param">s2</span><span class="key"> = </span><span class="method">Symbol</span><span class="word">(</span><span class="str">'symbol2'</span><span class="word">);</span><br><span class="spe">console</span><span class="word">.</span><span class="spe">log</span><span class="word">(</span><span class="spe">Symbol</span><span class="word">.</span><span class="method">keyFor</span><span class="word">(</span><span class="param">s2</span><span class="word">);</span><span class="com"> // 'undefined'</span></pre>
            <p>
                <span class="code">Symbol.for()</span> 方法登记的名字是全局环境的。
            </p>
            <p class="minititle">三、Symbol 内部的值</p>
            <p>
                <span class="tip">ES6</span> 提供了 11 个内置的 <span class="tip">Symbol</span> 值。下面列举一下，不具体展开介绍。
            </p>
            <p>
                <span class="code">Symbol.hasInstance</span>、
                <span class="code">Symbol.isConcatSpreadable</span>、
                <span class="code">Symbol.match</span>、
                <span class="code">Symbol.species</span>、
                <span class="code">Symbol.replace</span>、
                <span class="code">Symbol.search</span>、
                <span class="code">Symbol.split</span>、
                <span class="code">Symbol.iterator</span>、
                <span class="code">Symbol.toPrimitive</span>、
                <span class="code">Symbol.toStringTag</span>、
                <span class="code">Symbol.unscopables</span>。具体内容可以参照 <a href="http://es6.ruanyifeng.com/#docs/symbol" target="_blank">《ECMAScript 6 入门》 11-7 内置的 Symbol 值</a>。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


