<template>
    <div class="blog">
        <header>
            <p class="title">{{thisBlog.title}}</p>
            <p class="time">
                {{thisBlog.time}}
                <el-tag v-for="tags in thisBlog.tags" :key="tags.name" :type="tags.type">{{tags.name}}</el-tag>
            </p>
        </header>
        <section>
            <p class="subtitle">前言</p>
            <p v-html="thisBlog.brief">{{thisBlog.brief}}</p>
            <p class="subtitle">正文</p>
            <p>
                首先详细描述一下报错信息：
            </p>
            <p class="error">
                fatal: Unable to create 'e:/git/Android/XXXXXX/.git/index.lock': File e
                xists. <br>
                If no other git process is currently running, this probably means a
                git process crashed in this repository earlier. Make sure no other git
                process is running and remove the file manually to continue.
            </p>
            <p>
                从后面的报错描述中就可以看出知道如何解决了（英文不错的朋友可以无视我这段翻译）。
            </p>
            <p>
                如果当前没有其他的 <span class="tip">git</span> 进程 在运行，那就可能是由于 <span class="tip">git</span> 进程在更早之前产生了冲突。确保没有其他运行的 <span class="tip">git</span> 进程后，手动删除这个文件即可。
            </p>
            <p>
                在文件的视图中有可能找不到 <span class="tip">index.lock</span> 这个文件。使用 <span class="tip">git</span> 直接删除即可，方便快捷。
            </p>
            <pre class="cmd">rm -force ./.git/index.lock</pre>
            <p>
                注意：不要使用 <span class="tip">-f</span>，因为这会产生歧义，<span class="tip">-f</span> 可以表示 <span class="tip">-force</span>，也可以表示 <span class="tip">-filter</span>。
            </p>
        </section>
    </div>
</template>
<script>
    import blog from './../../../assets/js/blog';
    export default{
        ...blog
    }
</script>


