<template>
    <div id="my-search">
        <header>
            <el-input v-model="keyword"
                      icon="search"
                      placeholder="search for..." @change="querySearch"></el-input>
            <el-button type="text" @click="goBack">取消</el-button>
        </header>
        <div v-show="!relatedBlog.length">
            <p class="no-relevant">
                No relevant content.
            </p>
        </div>
        <div v-for="item in relatedBlog">
            <p class="search-content"
               @click="goToBlog(item.index)">{{item.value}}</p>
        </div>
    </div>
</template>
<script>
    export default {
        data(){
            return {
                relatedBlog:[],
                keyword:'',
            }
        },
        methods:{
            // 去空格转为小写搜索
            formatSearch(key){
                return key.toLowerCase().replace(/\s/g,'');
            },
            // 模糊搜索，搜索框提示内容
            querySearch() {
                const that = this;
                const key = that.formatSearch(that.keyword);
                if( key === '' ){
                    that.relatedBlog = [];
                    return false;
                }
                let _arr = [];
                that.$store.state.initialBlogData.forEach( (e,i) => {
                    if( that.formatSearch(e.brief).indexOf(key) !== -1 || that.formatSearch(e.title).indexOf(key) !== -1 ){
                        _arr.push({value:e.title,index:e.url});
                    }
                });
                that.relatedBlog = _arr;
            },
            // 前往所搜索的 blog
            goToBlog(url){
                this.searchVisInside = !this.searchVisInside;
                this.$router.push('/blog/blog_'+url);
            },
            // 回到上一页
            goBack(){
                this.$router.go(-1);
            },
        }
    }
</script>
<style>
    #my-search header{
        padding:10px;
    }
    #my-search .el-button.el-button--text{
        float:right;
    }
    #my-search header .el-input{
        width:calc(100% - 48px);
    }
    #my-search header .el-input__inner{
        border:none;
        -webkit-border-radius:0;
        -moz-border-radius:0;
        border-radius:0;
        background:#f9fafc;
        border-bottom:1px solid #bfcbd9;
    }
    #my-search .no-relevant{
        text-align:center;
        color:#8492A6;
    }
    #my-search .search-content{
        font-size:14px;
        color:#475669;
        border-bottom:1px solid #D3DCE6;
        padding:10px 0 10px 4px;
        margin:0 10px;
    }
</style>


